<template>
  <div class="orderinfo">
    <header class="head clearfix">
        <div @click="backbtn()" class="fl">
            <span class="fl headback"></span>
            <span class="fl"></span>
        </div>
        <h3 class="fl"></h3>
    </header>
    <div class="userInfoBox">
        <ul class="userInfo">
            <li class="item ">
                <strong ></strong>
                <input @blur="isName"  v-model="nameValue"   placeholder=""  type="text" value="nameValue" >
            </li>
             <li class="item ">
                <strong ></strong>
                <input  type="text"  @blur="isPhoneNumber"  v-model="phoneNumber" placeholder="" value="phoneNumber" >
           </li> 
            <li class="item ">
