<template>
    <div class="uploadimage">
        <header class="head clearfix">
            <div @click="backbtn()" class="fl">
                <span class="fl headback"></span>
                <span class="fl"></span>
            </div>
            <h3 class="fl"></h3>

            
        </header>
        <div class="imgbtnortext">
            <!--  -->
            <div v-show="imgsAddress.length<1"  class="upimg">
                <div style=" width:100% ; height:100%;"> 
                    <div class="show"> 
                        <div class="picture" :style="'backgroundImage:url('+headerImage+')'"></div> 
                    </div> 
                    <div style="width:100%;height:100%"> 
                        <!-- <input style="width:100%;height:100%;  opacity:0;" type="file" id="upload"  accept="image" @change="upload"> 
                        <label for="upload"></label>  -->

                        <van-uploader style="width:100%;height:100%;"       :after-read="onRead"     >
                          <van-icon      name="photograph" />
                        </van-uploader>
                    </div> 
                </div> 
            </div>
            <!--  -->
            <div class="showimge">

                <div  v-for="item,index in imgsAddress"    class="imgBox">
                  <img  :src="imgsAddress[index][0].thumbnail"   alt="">
                  <div class="zhezhao"></div>
                  <van-icon  @click="removeImg(index)"  class="closeImg" name="clear" />

                </div>
                <!-- <img src="@/assets/upimg2.png" alt=""> -->
                <div v-show="imgsAddress.length>0 && imgsAddress.length<3" class="isaddimg">
                    <span class="level"></span>
                    <span class="verticals"></span>
                    <div style=" width:100% ; height:100%;"> 
                      <div class="show"> 
                          <div class="picture" :style="'backgroundImage:url('+headerImage+')'"></div> 
                      </div> 
                      <div style="width:100%;height:100%"> 
                        <!--  <input style="width:100%;height:100%;  opacity:0;" type="file" id="upload"  accept="image" @change="upload"> 
                         <label for="upload"></label>  -->
                        <van-uploader :after-read="onRead">
                          <!-- <van-icon name="photograph" /> -->
                        </van-uploader>
                      </div> 
                    </div> 
                </div>
            </div>
            <div   class="text"></div>
        </div>
        <div class="recycle_description">

            <h4></h4>
            <div class="textarea_box">
                <textarea rows="3" maxlength="150"  v-model="textareaValue" placeholder="" cols="20"  >
                
                </textarea>
                <span>{{textareaValue.length}}/150</span>
            </div>
            <div class="nextbutton "  @click="getTextareaValue"  >
                <router-link  to="/estimate" ></router-link>
            </div>
        </div>
    </div>

</template>

<style>
.img {
  display: block;
  max-width: 100%;
  margin: 20px auto;
}
</style>
<script>

import Exif from "exif-js";
import api from '@/api/api.js'
import $ from 'jquery'
import axios from 'axios'

import "@/assets/createstyle/uploadimage.css";
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      headerImage   : "",
      textareaValue : '',   //
      number :0
    };
  },
  computed: mapGetters({

      imgsAddress : 'imgsAddress'

  }),
  methods: {
    backbtn() {
      //
      this.$router.go(-1);
      
    },
    removeImg(index){


        
        this.$store.dispatch('removeImg',index)


    },
    getTextareaValue(){

      this.$store.dispatch('changeTextareaValue',this.textareaValue)   //

    },
    onRead(file) {
      console.log(file)
      

      var formdata =new FormData();
          formdata.append('file',file.file);
          // formdata.append('file', this.headerImage);
          formdata.append ("name" , "util.upload");
          formdata.append ("version" , "1.0");
          formdata.append ("format" , "json");
          formdata.append ("token" , "");
      axios({
        url:'http://101.132.165.211:8080/ali/api',
        method:'post',
        data:formdata,
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res)=>{

        console.log(res.data)
        this.$store.dispatch('addImgsUrl',res.data.data)
        

      })

      
    },
   
  }
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
.show {
  width: 100px;
  height: 100px;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
  border: 1px solid #d5d5d5;
}
.picture {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
</style>


